<template>
  <div class="s-canvas">
    <canvas id="s-canvas" :width="contentWidth" :height="contentHeight"></canvas>
  </div>
</template>
<script>
export default {
  name: 'SIdentify',
  props: {
    identifyCode: {
      type: String,
      default: 'asdf'
    },

  },
  data(){
    return {
      fontSizeMin: 16,
      fontSizeMax: 40,
      backgroundColorMin: 180,
      backgroundColorMax:240,
      colorMin: 50,
      colorMax: 160,
      lineColorMin:40,
      lineColorMax: 180,
      dotColorMin: 0,
      dotColorMax: 255,
      contentWidth: 140,
      contentHeight: 40
    }
  },
  methods: {
    // 
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min)
    },
    // 
    randomColor(min, max) {
      let r = this.randomNum(min, max)
      let g = this.randomNum(min, max)
      let b = this.randomNum(min, max)
      return 'rgb(' + r + ',' + g + ',' + b + ')'
    },
    drawPic(code) {
      let canvas = document.getElementById('s-canvas')
      let ctx = canvas.getContext('2d')
      // ctx.textBaseline = 'bottom'
      // 
      ctx.fillStyle = this.randomColor(this.backgroundColorMin, this.backgroundColorMax) //
      ctx.strokeStyle = this.randomColor(this.backgroundColorMin, this.backgroundColorMax) //
      ctx.fillRect(0, 0, this.contentWidth, this.contentHeight) // 0 0 width height x y   
      ctx.strokeRect(0,0,this.contentWidth, this.contentHeight) //  0 0 width height x y   
      // ctx.clearRect(50,0,this.contentWidth,this.contentHeight)  //
      // 
      for (let i = 0; i < code.length; i++) {
        this.drawText(ctx, code[i], i)
      }
      this.drawLine(ctx)
      this.drawDot(ctx)
    },
    drawText(ctx, txt, i) {
      ctx.fillStyle = this.randomColor(this.colorMin, this.colorMax)
      ctx.font = this.randomNum(this.fontSizeMin, this.fontSizeMax) + 'px SimHei' //
      ctx.textBaseline = 'alphabetic' //
      let x = (i + 1) * (this.contentWidth / (this.identifyCode.length + 1))
      let y = this.randomNum(this.fontSizeMax, this.contentHeight - 15)
      var deg = this.randomNum(-45, 45)
      // 
      ctx.translate(x, y)//  
      ctx.rotate(deg * Math.PI / 180) // 
      ctx.fillText(txt, 0, 0)
      // 
      ctx.rotate(-deg * Math.PI / 180)
      ctx.translate(-x, -y)
    },
    drawLine(ctx) {
      // 
      for (let i = 0; i < 8; i++) {
        ctx.strokeStyle = this.randomColor(this.lineColorMin, this.lineColorMax)
        ctx.beginPath() //
        ctx.moveTo(this.randomNum(0, this.contentWidth), this.randomNum(0, this.contentHeight)) //x,y
        ctx.lineTo(this.randomNum(0, this.contentWidth), this.randomNum(0, this.contentHeight)) // x,y x,y
        ctx.stroke() // 
        // ctx.closePath() //
      }
    },
    drawDot(ctx) {
      // 
      for (let i = 0; i < 100; i++) {
        ctx.fillStyle = this.randomColor(0, 255)
        ctx.beginPath()
        // x,y,radiusstartAngle,endAngle,anticlockwise // x,y radius startAngleendAngle
        ctx.arc(this.randomNum(0, this.contentWidth), this.randomNum(0, this.contentHeight), 1, 0, 2 * Math.PI)
        ctx.fill() //
      }
    },
    async getCode(){
      let result = await this.$axios.userControllerList.getCode();
      if (result) {
        this.$store.commit('modFormCode',result.data.code)
        this.drawPic(result.data.code)
      }else {
        this.$store.commit('modFormCode',"xwr5")
        this.drawPic("xwr5")
      }
    }
  },
  watch: {
    identifyCode() {
      this.drawPic()
    }
  },
  async mounted() {
    await this.getCode()
  }
}
</script>

